.name {
  text-align: center;
  color: #ffffff;
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  margin-top: 21px;
  margin-bottom: 9px;
  line-height: 30px;
  font-size: auto;
  letter-spacing: 2px;
}
.job {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: auto;
  line-height: 15px;
  text-align: center;
  color: #f3bf99;
  margin-top: 6px;
  margin-bottom: 7px;
  letter-spacing: 1px;
}
.link_aligner {
  font-family: "Inter";
  text-align: center;
  word-wrap: break-word;
}
.website {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 13.24px;
  line-height: 150%;
  color: #f5f5f5;
  text-decoration: none;
  letter-spacing: 1px;
}
.two_buttons {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.button1 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  padding: 9px 13px 9px 11px;
  gap: 8px;
  margin-right: 5px;
}
.button1 a {
  text-decoration: none;
  color: rgb(14, 8, 0);
}
.favi1 {
  width: 17px;
  height: 19px;
}
.linkedin__button {
  text-decoration: none;
}
.form {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.button2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 9px 13px 9px 11px;
  gap: 8px;
  background: #2867b2;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
}
.button2 a {
  color: rgb(0, 0, 0);
}
.favi2 {
  width: 20px;
  height: 20px;
}
.text_box {
  margin-right: 5%;
  margin-left: 5%;
  color: #f5f5f5;
}
.text_box h3 {
  margin-bottom: 5px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;

  color: #f5f5f5;
}
.text_box p {
  margin-top: 0px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 13.24px;
  line-height: 150%;
  color: #dcdcdc;

  word-wrap: break-word;
}
.last_p {
  margin-bottom: 25px;
}
.bto {
  background-color: #161619;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  /* flex-wrap: wrap; */
  align-items: center;
  border-radius: 10px;
}
.bto a {
  color: #1d92af;
  padding: 12px;
}
